*, *::before, *::after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

html, body {
  height: 100%;
  margin: 0;
  font-family: Roboto, Arial, sans-serif;
  font-size: 20px;
  line-height: 1.2;
  font-weight: normal; }

.list {
  border-left: 1px solid #2ef6fa;
  margin-left: 20px;
  padding-left: 0; }
  .list .list__item {
    list-style: none;
    padding: 0; }

.EditableLabel {
  display: block;
  width: 100%;
  padding: 10px;
  font-size: 20px;
  border-bottom: 1px solid #c1c7ea;
  color: rgba(0, 0, 0, 0.6);
  text-shadow: rgba(0, 0, 0, 0.2) 2px 6px 5px, rgba(255, 255, 255, 0.4) 0 -4px 30px;
  cursor: pointer; }

.EditableInput {
  display: block;
  width: 100%;
  padding: 10px;
  font-size: 20px;
  border: none;
  border-bottom: 1px solid #c1c7ea;
  outline: none; }



